import BScroll from 'better-scroll'
export default {
  props: {
    cities: {
      type: Object,
      required: true
    },
    hotCities: {
      type: Array,
      default: () => []
    },
    letter: {
      type: String,
      default: ''
    },
    currentCity: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      scroll: null
    }
  },
  methods: {
    handleClick (city) {
      this.$emit('on-click-city', city)
    }
  },
  mounted () {
    let wrapper = document.querySelector('.wrapper')
    // eslint-disable-next-line
    this.scroll = new BScroll(wrapper, {
      click: true
    })
  },
  watch: {
    letter (newVal) {
      if (newVal) {
        const element = this.$refs[newVal][0]
        this.scroll.scrollToElement(element, 200)
      }
    }
  }
}



